import $router from '@/router'
export const gotoNewPage = function(path) {
  $router.push(`prevention/${path}`)
}

export const vaccineType = function(row) {
  switch (row.vaccine_type) {
    case 1:
      return ''
    case 2:
      return ''
    case 3:
      return ''
  }
}

export const vaccineClass = function(row) {
  switch (row.vaccine_class) {
    case 1:
      return ''
    case 2:
      return ''
  }
}

export const getSimpleText = function(html) {
  const re1 = new RegExp('<.+?>', 'g') // html"g"
  const msg = html.replace(re1, '') // 
  if (msg.length > 38) {
    return msg.slice(0, 38) + '...'
  }
  return msg
}

