<template>
  <div class="recipes-container">
    <div class="success-container">
      <div class="success-title" style="margin-bottom: 30px" />
      <div class="success-item">
        <div class="label">
          <div class="avatar">
            <el-avatar
              :size="'small'"
              :src="taskDetail.user && taskDetail.user.avatar"
            />
          </div>
          :
        </div>
        <div class="content">
          {{ userName }}&nbsp;&nbsp;
          <svg-icon v-if="taskDetail.is_replace" icon-class="replace" />
        </div>
      </div>
      <div class="success-item baby-item">
        <div class="label">:</div>
        <div class="content">
          {{ taskDetail.family_member && taskDetail.family_member.name }}
        </div>
      </div>
      <div class="success-item">
        <div class="label">:</div>
        <div class="content">
          {{ taskDetail.title }}
        </div>
      </div>
      <div class="success-item" v-if="taskDetail.subType === 4">
        <div class="label">:</div>
        <div class="content">
          {{ taskDetail.desc ? taskDetail.desc : '' }}
        </div>
      </div>
      <div class="success-item">
        <div class="label">:</div>
        <div class="content">
          {{ createTime }}
        </div>
      </div>
      <div class="success-item">
        <div class="label">:</div>
        <div class="content">
          {{ executeDate }}
        </div>
      </div>
      <div v-if="taskDetail.subType === 5">
        <div class="success-item">
          <div class="label">:</div>
          <div class="content user-replay">
            {{ taskDetail.task_science_schedule.desc }}
          </div>
        </div>
        <el-row style="margin-left: 200px">
          <el-col
            v-for="(image, index) in taskDetail.task_science_schedule.icons"
            :key="index"
            :span="2"
          >
            <img
              :src="image.url"
              width="50"
              height="50"
              @click="showImage(image, taskDetail.task_science_schedule.icons)"
            />
          </el-col>
        </el-row>
      </div>
      <div v-if="taskDetail.subType === 6" class="vaccine-reference">
        <div class="success-item">
          <div class="label">:</div>
          <div class="content user-replay">
            <vaccine-table
              :table-data="taskDetail.task_vaccine_schedule.curTask"
            ></vaccine-table>
          </div>
        </div>
        <div
          v-for="(item, index) in taskDetail.task_vaccine_schedule.curTask"
          :key="'cur' + index"
        >
          <div class="success-item margin-bottom-10">
            <div class="label"><span v-if="index === 0">:</span></div>
            <div class="content user-replay">
              {{ item.desc }}
            </div>
          </div>
          <el-row style="margin-left: 200px" class="margin-bottom-30">
            <el-col
              v-for="(image, index) in item.icons"
              :key="'curIcon' + index"
              :span="2"
            >
              <img
                :src="image.url"
                width="50"
                height="50"
                @click="showImage(image, item.icons)"
              />
            </el-col>
          </el-row>
        </div>
        <div class="success-item">
          <div class="label"></div>
          <div class="content user-replay">
            <vaccine-table
              :table-data="taskDetail.task_vaccine_schedule.nextTask"
            ></vaccine-table>
          </div>
        </div>
        <div
          v-for="(item, index) in taskDetail.task_vaccine_schedule.nextTask"
          :key="'next' + index"
        >
          <div class="success-item margin-bottom-10">
            <div class="label"><span v-if="index === 0">:</span></div>
            <div class="content user-replay">
              {{ item.desc }}
            </div>
          </div>
          <el-row style="margin-left: 200px" class="margin-bottom-30">
            <el-col
              v-for="(image, index) in item.icons"
              :key="'nextIcon' + index"
              :span="2"
            >
              <img
                :src="image.url"
                width="50"
                height="50"
                @click="showImage(image, item.icons)"
              />
            </el-col>
          </el-row>
        </div>
        <div class="tips">
          <div class="tips-title">
            
          </div>
          <div class="tips-content">
            
          </div>
        </div>
      </div>

      <div class="divider-wrap">
        <el-divider></el-divider>
      </div>
      <div class="success-item">
        <div class="label">&nbsp;</div>
        <div class="content"></div>
      </div>
      <div class="success-item">
        <div class="label">:</div>
        <div class="content reason-line-height">
          {{ closeTime }}
        </div>
      </div>
      <div class="success-item">
        <div class="label">:</div>
        <div class="content">
          <div>
            {{ taskDetail.closed_reason }}
          </div>
          <div
            v-if="taskDetail.closed_reason_message"
            class="margin-top-10 margin-bottom-minus30"
          >
            {{ taskDetail.closed_reason_message }}
          </div>
        </div>
      </div>
      <div class="success-item">
        <div class="label">:</div>
        <div class="content">
          {{ taskDetail.closed_by && taskDetail.closed_by.name }}
        </div>
      </div>
    </div>
    <!---->
    <monitor
      ref="monitor"
      :monitor-info="monitorInfo"
      :current-image-url-arr="curUrlList"
    />
  </div>
</template>

<script>
import { checkDesc } from '@/api/healthManagement'
import VaccineTable from './VaccineTable'
import Monitor from '@/components/Monitor'

import _ from 'lodash'
export default {
  name: 'TaskDetail',

  data() {
    return {
      curUrlList: [],
      monitorInfo: {},
      currentTask: {},
      taskDetail: {}
    }
  },
  components: {
    VaccineTable,
    Monitor
  },
  mounted() {
    this.getTaskDetail()
  },
  computed: {
    userName() {
      return _.get(this.taskDetail, 'user.remark_name')
        ? _.get(this.taskDetail, 'user.remark_name')
        : _.get(this.taskDetail, 'user.nickname')
    },
    createTime() {
      return this.formatTime(this.taskDetail.createTime)
    },
    closeTime() {
      return this.formatTime(this.taskDetail.closed_at)
    },
    executeDate() {
      return this.formatTime(this.taskDetail.executeTime, '{y}-{m}-{d}')
    }
  },
  methods: {
    // 
    showImage(image, urlList) {
      this.curUrlList = urlList.map(({ url }) => url)
      this.monitorInfo = {
        type: image.type,
        meta: { url: image.url }
      }
      this.$refs.monitor.show()
    },
    getTaskDetail() {
      checkDesc(this.$route.query.taskId).then(res => {
        if (res.code === 20000) {
          this.taskDetail = res.data
        }
      })
    }
  }
}
</script>
<style lang="scss">
.comment-content {
  .el-textarea {
    .el-input__count {
      bottom: 5px;
      background-color: transparent;
    }
  }
}
</style>
<style scoped lang="scss">
@import '@/styles/healthManagement/addtask.scss';
@import '@/styles/recipes/recipes.scss';
.tips {
  display: flex;
  align-items: flex-start;
  margin-top: 10px;
  margin-left: 150px;
  color: #f8747f;
  margin-bottom: -20px;

  .tips-title {
    width: 120px;
    font-weight: bold;
    text-align: right;
  }

  .tips-content {
    padding-left: 10px;
  }
}
.table-img {
  margin-bottom: 20px;
  margin-left: 200px;
}
.user-replay {
  margin-top: -4px;
  line-height: 24px;
}
.divider-wrap {
  margin: 0 auto;
  width: 80%;
}
.label {
  font-weight: bold;
}
.recipes-container {
  margin-left: 20px;
  width: 100%;
  .recipe__body__notes {
    white-space: pre-line;
  }
  .recipes-image {
    margin-left: 200px;
    padding-top: 30px;
    width: 640px;
  }
}
.comment {
  padding-left: 55px;
  color: #17233d;
  .comment-item {
    padding: 0 55px;
    font-size: 16px;
    margin-bottom: 30px;
    .comment-label {
      margin-top: 10px;
      line-height: 50px;
      color: #0085ff;
      cursor: pointer;
    }
    .comment-content {
      padding-right: 100px;
    }
  }
}
.success-container {
  margin-left: -100px;
  min-height: 800px;
}
.baby-item {
  position: absolute;
  top: 0;
  right: 350px;
  width: 500px;
}
.like {
  color: #ffb718;
}
.unlike {
  color: #999;
}
.time-stamp {
  margin-top: -50px;
  padding-left: 200px;
  line-height: 50px;
  color: #999;
}
</style>
